import Vue from "vue";
import Router from "vue-router";
// import Home from "@/components/Home";
// import Organization from "@/pages/OrgManage/main";
// import Role from "@/pages/RoleManage/Role";
// import User from "@/pages/UserManage/User";
// import UnAuthorized from "@/components/UnAuthorized"
import Demo from "@/pages/Demo/Demo"
// import Schedule from "@/pages/ScheduleManager/Schedule"

Vue.use(Router)

const routerConfig = {
  routes: [
    // {
    //   path: '/org/:id',
    //   name: 'org',
    //   component: Organization
    // },
    // {
    //   path: '/role/:id',
    //   name: 'role',
    //   component: Role
    // },
    // {
    //   path: '/user/:id',
    //   name: 'user',
    //   component: User
    // },
    // {
    //   path: '/unauthorized',
    //   name: 'unauthorized',
    //   component: UnAuthorized
    // },
    // {
    //   path: '/test',
    //   name: 'test',
    //   component: Home
    // },
    {
      path: '/demo',
      name: 'demo',
      component: Demo
    },
    // {
    //   path: '/schedule',
    //   name: 'schedule',
    //   component: Schedule
    // }
  ]
};

const unlimitedPath = [
  '/unauthorized',
  '/test',
  '/demo',
  '/schedule'
]

const router = new Router(routerConfig);

router.initMenuList = function (menuList) {
  this.beforeEach((to, from, next) => {
    var menuCodes = new Array();
    menuList.forEach((menuInfo) => {
      menuCodes.push(menuInfo.code);
    });

    //
    if (to.path == "/") {
      // routeConfig
      for (var routeInfo of routerConfig.routes) {
        if (menuCodes.indexOf(routeInfo.name) != -1) {
          next(routeInfo.path.replace(":id", routeInfo.name));
          return;
        }
      }
    }

    //
    if (unlimitedPath.indexOf(to.path) != -1) {
      next();
      return;
    }

    //
    if (menuCodes.indexOf(to.name) == -1) {
      next("/unauthorized");
      return;
    }

    next();
  })
}

export default router;



