        this.stepWindowConfig.show = true;
        this.stepWindowConfig.active = 1;
        this.windowConfig.form.parentId = "";
      },

      // 
      getFiles() {
        console.log(this.uploadConfig.files);
      },

      //
      dialogFn(index, item) {
        this.$refs.dialog.contextMenu(this.$refs.colors.childNodes[index], this.menuJson, item)
      }
    },
    directives: {
      dialog: {
        bind: function (el, binding, vnode) {
        },
        inserted: function (el, binding, vnode) {
          // console.log(vnode.context)
          var _this = vnode.context
          _this.dialogFn(binding.value.index, binding.value.item)
        },
      }
    },
    mounted() {
      var c = setInterval(()=>{
        this.percentage+=1;
        if(this.percentage == 100){
          clearInterval(c);
        }
      },100);
      // this.$http.get("./restful/caorg/getAll").then(
      //   (res) => {
      //     this.selectConfig.options = res.body.content;
      //   }
      // )
    },
    watch: {
      "tooltipConfig.showTooltip": function (val) {
        // tooltipWindow
        if (val == false && this.tooltipConfig.tooltipType == "success") {
          this.windowConfig.show = false;
        }
      }
    },
    components: {
      IDatetimePicker,
      Checkbox,
      MyDialog,
      ImgUploader,
      IRadio,
      ISelect,
      ISlider,
      ISwitch,
      ITimePicker,
      ITransfer,
      MyTooltip,
      Tree,
      MyWindow,
      // TreeSelect,
      ITextarea,
      IInputbox,
      FileUploader,
      TabPane,
      Tabs,
      IForm,
      IFormItem,
      sideDialog,
      ISteps,
      IStep,
      IButton,
      IStepTabs,
      IStepTabPanel,
      IWindow,
      IProgress,
      ILoading,
      IOption
    }
  }
</script>

<style scoped>
  .normal-demo {
    font-family: "";
    padding-top: 15px;
    background: #ffffff;
    border-top: 2px dashed lightgray;
    padding-bottom: 30px;
  }

  .title {
    width: 400px;
    font-size: 25px;
    text-shadow: 8px 8px 10px black;
    margin: 20px auto;
    text-align: center;
  }

  .common {
    font-family: "";
    margin-left: 50px;
    padding-bottom: 15px;
  }

  .component-name {
    font-size: 20px;
    font-weight: bold;
  }

  .normal-component {
    padding: 3px;
    text-indent: 2em;
    font-size: 16px;
  }

  .description {
    color: #999999;
    padding-left: 33px;
  }

  .color {
    color: #fff;
    width: 100px;
    text-align: center;
    line-height: 30px;
    background-color: #ccc;
    border: 1px solid #000;
  }
</style>



